export default {
  layout: "Dum-content",
  data() {
    return {
      facilitie: {
        title: "",
        description: "",
        created_by: this.$auth.user.name,
      },
      errors: {},
    };
  },
  methods: {
    addFacilitie() {
      this.$axios
        .$post("/facilitie/add", this.facilitie)
        .then((res) => {
          this.facilitie = "";
          this.errors = {};
          this.$toaster.success(res.message);
          this.$router.push("/dum-ac/facilitie");
        })
        .catch((error) => {
          this.errors = error.response.data.errors;
        });
    },
  },
};
